import React, { useState } from 'react';
import {
  MintTokens,
  setMinter,
  checkIsMinter,
} from '../blockchain/functions/functions';
import {
  Container,
  Button,
  Text,
  Input,
  NumberInput,
  NumberInputField,
  Spinner,
} from '@chakra-ui/react';

function Token() {
  const [tokenAddress, setTokenAddress] = useState('');
  const [isSending, setIsSending] = useState(false);
  const [mintInfo, setMintInfo] = useState({
    recipient: '',
    amount: '',
  });
  const [isMinterInfo, setIsMinterInfo] = useState({
    address: '',
    status: '',
  });
  const [checkMinter, setCheckMinter] = useState({
    address: '',
    status: '',
  });

  const handleMint = async () => {
    setIsSending(true);
    let receipt = await MintTokens(
      mintInfo.recipient,
      mintInfo.amount,
      tokenAddress
    );
    if (receipt) {
      window.alert(
        `Transaction Succesful! ${
          receipt.events.Transfer.returnValues.value / 1e18
        } tokens minted to the address ${
          receipt.events.Transfer.returnValues.to
        }`
      );
      console.log(receipt);
    }
    setIsSending(false);
  };

  const handleSetMinter = async () => {
    setIsSending(true);
    if (isMinterInfo.status !== 'true' && isMinterInfo.status !== 'false') {
      window.alert('Select true or false as status.');
      return;
    }
    let receipt = await setMinter(
      isMinterInfo.address,
      isMinterInfo.status,
      tokenAddress
    );
    if (receipt) {
      window.alert('The Transaction was Succesful');
      console.log(receipt);
    }
    setIsSending(false);
  };

  const handleCheckMinter = async () => {
    let receipt = await checkIsMinter(checkMinter.address, tokenAddress);
    try {
      setCheckMinter({ ...setCheckMinter, status: receipt });
    } catch (error) {
      console.log(error);
    }
  };

  return (
    <Container maxW="container.xl">
      <Container maxW="container.sm" marginBottom="5">
        <Text>Token Address</Text>
        <Input
          placeholder="Token Address"
          value={tokenAddress}
          onChange={e => setTokenAddress(e.target.value)}
        />
      </Container>

      <Container maxW="container.sm" marginBottom="5">
        <Text>Mint Token</Text>
        <Input
          placeholder="Recipient Address"
          value={mintInfo.recipient}
          onChange={e =>
            setMintInfo({ ...mintInfo, recipient: e.target.value })
          }
        />
        <NumberInput>
          <NumberInputField
            placeholder="Amount to Mint"
            value={mintInfo.amount}
            onChange={e => setMintInfo({ ...mintInfo, amount: e.target.value })}
          />
        </NumberInput>
        <Button onClick={handleMint}>
          {isSending ? <Spinner /> : 'Mint'}{' '}
        </Button>
      </Container>

      <Container maxW="container.sm" marginBottom="5">
        <Text>Set Minter Role</Text>
        <Input
          placeholder="Minter Address"
          value={isMinterInfo.address}
          onChange={e =>
            setIsMinterInfo({ ...isMinterInfo, address: e.target.value })
          }
        />
        <Input
          placeholder="Status: true or false"
          value={isMinterInfo.status}
          onChange={e =>
            setIsMinterInfo({ ...isMinterInfo, status: e.target.value })
          }
        />
        <Button onClick={handleSetMinter}>
          {' '}
          {isSending ? <Spinner /> : 'Set Minter'}
        </Button>
      </Container>

      <Container maxW="container.sm" marginBottom="5">
        <Text>Check Minter Role</Text>
        <Input
          placeholder="Minter Address"
          value={checkMinter.address}
          onPaste={e =>
            setCheckMinter({ ...checkMinter, address: e.target.value })
          }
          onChange={e =>
            setCheckMinter({ ...checkMinter, address: e.target.value })
          }
        />
        <Input
          placeholder="Status: true or false"
          value={checkMinter.status}
          readOnly
        />
        <Button onClick={handleCheckMinter}>Check Minter Role</Button>
      </Container>
    </Container>
  );
}

export default Token;
