import React, { useState } from 'react';
import {
  newLimits,
  newPassword,
  shouldMint,
  setTokenContract,
  claim,
  Info,
} from '../blockchain/functions/functions';
import {
  Container,
  Button,
  Text,
  Input,
  NumberInput,
  NumberInputField,
  FormControl,
  Spinner,
} from '@chakra-ui/react';
import web3 from '../blockchain/web3';

function Password() {
  const [contractAddress, setContractAddress] = useState('');
  const [isSending, setIsSending] = useState(false);
  const [hash, setHash] = useState({
    text: '',
    result: '',
  });
  const [info, setInfo] = useState({
    tokenLimit: '',
    addressLimit: '',
    countClaims: '',
    owner: '0x00',
    addressToken: '',
    isMinting: '',
  });
  const [newParams, setNewParams] = useState({
    tokenLimit: '',
    addressLimit: '',
    addressToken: '',
    isMinting: '',
    password: '',
  });
  const [isEditing, setIsEditing] = useState(false);

  const handleClaim = async () => {
    setIsSending(true);
    let receipt = await claim(hash.result, contractAddress);

    if (receipt) {
      window.alert('Transaction Succesfull');
      console.log(receipt);
    }
    setIsSending(false);
  };

  const handleNewLimits = async () => {
    let result = await newLimits(
      newParams.tokenLimit,
      newParams.addressLimit,
      contractAddress
    );
    if (result) {
      window.alert('Limits changed succesfully');
      console.log(result);
    }
  };

  const handleNewTokenContract = async () => {
    let result = await setTokenContract(
      newParams.addressToken,
      contractAddress
    );
    if (result) {
      window.alert('Token Contract changed succesfully');
      console.log(result);
    }
  };

  const handleNewPassword = async () => {
    let result = await newPassword(newParams.password, contractAddress);
    if (result) {
      window.alert('Password changed succesfully');
      console.log(result);
    }
  };

  const handleIsMinting = async () => {
    if (newParams.isMinting !== 'true' && newParams.isMinting !== 'false') {
      window.alert('New status should be true or false');
      return;
    }
    let result = await shouldMint(newParams.isMinting, contractAddress);
    if (result) {
      window.alert('Status changed succesfully');
      console.log(result);
    }
  };

  const getInfo = async () => {
    let result = await Info(contractAddress);
    if (result) {
      setInfo(result);
      console.log(result);
    }
  };

  return (
    <Container maxW="container.xl">
      <FormControl>
        <Container maxW="container.sm" marginBottom="5">
          <Text>Contract Address </Text>
          <Input
            placeholder="Contract Address"
            value={contractAddress}
            onChange={e => setContractAddress(e.target.value)}
          />
          <Button onClick={getInfo}>Get Info</Button>
        </Container>

        <Container maxW="container.sm" marginBottom="5">
          <Text>Claim Tokens</Text>
          <Text fontSize="sm">Text</Text>
          <Input
            placeholder="Text to Convert"
            value={hash.text}
            onChange={e =>
              setHash({
                ...hash,
                text: e.target.value,
                result: web3.utils.keccak256(e.target.value),
              })
            }
          />
          <br />
          <Text fontSize="sm">Hash</Text>
          <Input placeholder="Password" value={hash.result} readOnly />

          <Button onClick={handleClaim}>
            {isSending ? <Spinner /> : 'Claim'}
          </Button>
        </Container>

        {info.tokenLimit !== '' && (
          <Container>
            <Text>Token Limit is set on {info.tokenLimit / 1e18} </Text>
            <Text>
              Number of claims by account is {info.addressLimit}, at this moment
              you have claim {info.countClaims} times.{' '}
            </Text>
            <Text>The address of the token is {info.addressToken} </Text>
            <Text>Should mint option is {info.isMinting.toString()} </Text>
          </Container>
        )}

        {window.ethereum?.selectedAddress &&
          info.owner?.toUpperCase() ===
            window.ethereum?.selectedAddress?.toUpperCase() && (
            <Button onClick={() => setIsEditing(!isEditing)}>
              Manager Options
            </Button>
          )}

        {isEditing && (
          <Container>
            <Text>Edit Contract Parameters</Text>
            <Container maxW="container.sm" marginBottom="5">
              <NumberInput>
                <NumberInputField
                  placeholder="Token Limit"
                  value={newParams.tokenLimit}
                  onChange={e =>
                    setNewParams({ ...newParams, tokenLimit: e.target.value })
                  }
                />
                <Text fontSize="xs">Limit of Tokens To Claim</Text>
              </NumberInput>
              <NumberInput>
                <NumberInputField
                  placeholder="Address Limit"
                  value={newParams.addressLimit}
                  onChange={e =>
                    setNewParams({ ...newParams, addressLimit: e.target.value })
                  }
                />
                <Text fontSize="xs">Limit of Times an Account can Claim</Text>
              </NumberInput>
              <Button onClick={handleNewLimits}>Change Limits</Button>
            </Container>

            <Container maxW="container.sm" marginBottom="5">
              <Text>Token Contract</Text>
              <Input
                placeholder="Token Address"
                value={newParams.addressToken}
                onPaste={e =>
                  setNewParams({ ...newParams, addressToken: e.target.value })
                }
                onChange={e =>
                  setNewParams({ ...newParams, addressToken: e.target.value })
                }
              />
              <Button onClick={handleNewTokenContract}>
                Check Token Address
              </Button>
            </Container>
            <Container maxW="container.sm" marginBottom="5">
              <Text>Change Password</Text>
              <Input
                placeholder="New Password"
                value={newParams.password}
                onPaste={e =>
                  setNewParams({ ...newParams, password: e.target.value })
                }
                onChange={e =>
                  setNewParams({ ...newParams, password: e.target.value })
                }
              />
              <Button onClick={handleNewPassword}>Change Password </Button>
            </Container>
            <Container maxW="container.sm" marginBottom="5">
              <Text>Set is Minting</Text>
              <Input
                placeholder="New Status"
                value={newParams.isMinting}
                onChange={e =>
                  setNewParams({ ...newParams, isMinting: e.target.value })
                }
              />
              <Button onClick={handleIsMinting}>Change Status </Button>
            </Container>
          </Container>
        )}
      </FormControl>
    </Container>
  );
}

export default Password;
