import React, { useState } from 'react';
import { deployPassword, deployToken } from '../blockchain/functions/functions';
import {
  Container,
  Button,
  Text,
  Input,
  NumberInput,
  NumberInputField,
  Spinner,
} from '@chakra-ui/react';
import web3 from '../blockchain/web3';

function Deployer() {
  const [tokenInfo, setTokenInfo] = useState({
    name: '',
    symbol: '',
  });
  const [passwordInfo, setPasswordInfo] = useState({
    toknLimit: '',
    addrLimit: '',
    tknAddr: '',
  });
  const [hash, setHash] = useState({
    text: '',
    result: '',
  });
  const [isSending, setIsSending] = useState(false);

  const handleDeployToken = async () => {
    setIsSending(true);
    let receipt = await deployToken(tokenInfo.name, tokenInfo.symbol);
    if (receipt) {
      window.alert(
        `Token Deployed to the Address: ${receipt.events.tokenDeploy.returnValues.newTokenAddress}... Please Save this Address.`
      );
      console.log(receipt);
    }
    setIsSending(false);
  };
  const handleDeployPassword = async () => {
    setIsSending(true);
    if (checkFields()) {
      let receipt = await deployPassword(
        hash.result,
        passwordInfo.toknLimit,
        passwordInfo.addrLimit,
        passwordInfo.tknAddr
      );
      if (receipt) {
        console.log(receipt);
        window.alert(
          `Contract Deployed to the Address: ${receipt.events.passwordDeploy.returnValues.newPasswordAddress}... Please Save this Address.`
        );
      }
    }
    setIsSending(false);
  };

  const checkFields = () => {
    let { password, toknLimit, addrLimit, tknAddr } = passwordInfo;
    if (
      password !== '' &&
      toknLimit !== '' &&
      addrLimit !== '' &&
      tknAddr !== ''
    ) {
      return true;
    } else {
      window.alert('please complete all the fields');
      return false;
    }
  };

  return (
    <Container maxW="container.xl">
      <Container maxW="container.sm" marginBottom="5">
        <Text>Deploy Token</Text>
        <Input
          placeholder="Name"
          value={tokenInfo.name}
          onChange={e =>
            setTokenInfo({
              ...tokenInfo,
              name: e.target.value,
            })
          }
        />
        <Input
          placeholder="Symbol"
          value={tokenInfo.symbol}
          onChange={e =>
            setTokenInfo({
              ...tokenInfo,
              symbol: e.target.value,
            })
          }
        />
        <Button onClick={handleDeployToken}>
          {' '}
          {isSending ? <Spinner /> : 'Deploy'}
        </Button>
      </Container>
      <Container maxW="container.sm" marginBottom="5">
        <Text>Deploy Password Contract</Text>
        <Text fontSize="sm">Text</Text>
        <Input
          placeholder="Text to Convert"
          value={hash.text}
          onChange={e =>
            setHash({
              ...hash,
              text: e.target.value,
              result: web3.utils.keccak256(e.target.value),
            })
          }
        />
        <br />
        <Text fontSize="sm">Hash</Text>
        <Input placeholder="Password" value={hash.result} readOnly />
        <NumberInput>
          <NumberInputField
            placeholder="Token Limit"
            value={passwordInfo.toknLimit}
            onChange={e =>
              setPasswordInfo({
                ...passwordInfo,
                toknLimit: e.target.value,
              })
            }
          />
        </NumberInput>
        <NumberInput>
          <NumberInputField
            placeholder="Address Limit"
            value={passwordInfo.addrLimit}
            onChange={e =>
              setPasswordInfo({
                ...passwordInfo,
                addrLimit: e.target.value,
              })
            }
          />
        </NumberInput>
        <Input
          placeholder="Token Address"
          value={passwordInfo.tknAddr}
          onChange={e =>
            setPasswordInfo({ ...passwordInfo, tknAddr: e.target.value })
          }
        />
        <Button onClick={handleDeployPassword}>
          {' '}
          {isSending ? <Spinner /> : 'Deploy'}
        </Button>
      </Container>
      <Container maxW="container.sm" marginBottom="5"></Container>
    </Container>
  );
}

export default Deployer;
