import web3 from '../web3';
import ERC20 from '../interfaces/ERC20';
import Password from '../interfaces/password';
import Deployer from '../interfaces/deployer';
import hash from '../interfaces/hash';

const { ethereum } = window;

export const ethEnabled = async () => {
  try {
    let user = await ethereum.request({ method: 'eth_requestAccounts' });
    return user;
  } catch (error) {
    window.alert(error.message);
  }
};

export const deployPassword = async (
  _password,
  _tokLimit,
  _addrLimit,
  _tokenAddress
) => {
  let instance = await Deployer();
  let limit = web3.utils.toWei(_tokLimit);
  try {
    let receipt = await instance.methods
      .createPassword(_password, limit, _addrLimit, _tokenAddress)
      .send({ from: window.ethereum.selectedAddress });
    return receipt;
  } catch (error) {
    window.alert(error.message);
  }
};

