export const MintTokens = async (_to, _amount, _contractAddress) => {
  let instance = await ERC20(_contractAddress);
  try {
    let amount = web3.utils.toWei(_amount);
    let receipt = await instance.methods
      .mint(_to, amount)
      .send({ from: window.ethereum.selectedAddress });
    return receipt;
  } catch (error) {
    window.alert(error.message);
  }
};

